.merc-btn {
    border:0px;
    min-width: 130px;
    height: 48px;
    border-radius: 24px;
    display:inline-block;
    font-family: 'Arial Bold', 'Arial';
    font-size:1em;
    font-weight:bold;
    color:#6F6F6F;
    text-decoration:none;
    text-transform:uppercase;
    text-align:center;
    line-height: 27px;
    position:relative;
    cursor:pointer;
    background: #F8F8F8;
    text-align: left;
  }
  .circle {
    height: 40px;
    width: 40px;
    background-color: #FFFFFF;
    border-radius: 50%;
    display: inline-table;
    vertical-align: middle;
    //line-height: 40px;
    margin-top: 0.5px;
    margin-bottom: 2.5px;
    margin-left: -2px;
    color: #CCCCCC;
  }
  .label-key{
    vertical-align: middle;
    line-height: 48px;
    margin-left: 10px;
    text-align: center;
    width: 800%;
    margin-right: 10px;
  }
  .icon-merc{
    vertical-align: middle;
    //line-height: 40px;
  }
